// when word in upperCase you can change this value
@mixin animation-set($duration) {
  animation-name: circle;
  animation-duration: $duration;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  transform-origin: bottom;
}

@mixin size-box($height, $width) {
  height: $height;
  width: $width;
}

@mixin elem_position-Sizes($height, $width) {
  position: absolute;
  bottom: $circle;
  left: $circle;

  @include size-box($height, $width);
}

@mixin elem_bacgColor-borRadius($color, $radius) {
  background-color: $color;
  border-radius: $radius;
}
